import create from "../../../_snowpack/pkg/zustand.js";
import {combine} from "../../../_snowpack/pkg/zustand/middleware.js";
import {persist} from "./store-persistence-middleware.js";
export const usePreferencesStore = create(persist(combine({showRuler: false}, (set) => ({
  toggleRulerPreference: () => set((state) => ({showRuler: !state.showRuler}))
})), localStorage, "PREFERENCES"));
export const useAuthStore = create(persist(combine({
  token: ""
}, (set) => ({
  login: (token) => set((store) => ({token})),
  logout: () => set(() => ({token: ""}))
})), localStorage, "AUTH"));
export const useFontStore = create(persist(combine({
  font: {}
}, (set) => ({
  setLetter: (letter, path) => {
    set(({font}) => ({font: {...font, [letter]: path}}));
  },
  setFont: (font) => set(() => ({font})),
  remove: () => set(() => ({font: {}}))
})), localStorage, "FONT"));
export const useFontCreationProgressStore = create(persist(combine({
  step: 0
}, (set) => ({
  setStep: (newStep) => {
    set(() => ({step: newStep}));
  }
})), localStorage, "FONT_CREATION_PROGRESS"));
export const usePostLikesStore = create(persist(combine({
  postsLikedIds: []
}, (set) => ({
  addPost: (postId) => {
    set((store) => ({postsLikedIds: [...store.postsLikedIds, postId]}));
  },
  removePost: (postId) => {
    set((store) => ({
      postsLikedIds: store.postsLikedIds.filter((id) => id !== postId)
    }));
  },
  syncWithApi: (apiIds) => {
    set(() => ({postsLikedIds: apiIds}));
  }
})), localStorage, "POST_LIKE_IDS"));
