import {useHistory} from "../../../_snowpack/pkg/react-router-dom.js";
import {environment} from "../environment.js";
import {useAuthStore} from "../store/global-store.js";
export const useHttpClient = () => {
  const history = useHistory();
  const token = useAuthStore((store) => store.token);
  const logoutAction = useAuthStore((store) => store.logout);
  const request = async ({method, uri, headers, body, withAuth}) => {
    const url = environment.apiUrl + uri;
    headers = {
      ...headers,
      mode: "cors",
      "Content-Type": "application/json",
      ...withAuth ? {Authorization: `Bearer ${token}`} : {}
    };
    body = JSON.stringify(body);
    const response = await fetch(url.toString(), {
      headers,
      method,
      body
    });
    response.clone().json().then((resolved) => {
      if (resolved?.message?.includes("Invalid token")) {
        logoutAction();
        history.push("/");
      }
    });
    return response;
  };
  return {request};
};
