export function convertToTTF(font) {
  return new Promise((resolve, reject) => {
    try {
      const worker = new Worker("./font-worker.ts");
      worker.postMessage(font);
      worker.onmessage = (e) => {
        resolve(e.data);
      };
    } catch (error) {
      reject("Failed to create .ttf font");
    }
  });
}
